import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {			
		return {
			title:'',
			img: "../../static/img/1201.png",
			extraIcon1: {
				color: '#FFAC60',
				size: '22',
				type: 'contact-filled'
			},
			extraIcon2: {
				color: '#FFAC60',
				size: '22',
				type: 'gear-filled'
			},
			extraIcon3: {
				color: '#FFAC60',
				size: '22',
				type: 'circle-filled'
			}
		}
	},
	onLoad() {
		uni.getStorage({
			key:'loginData',success: res =>{
				this.title = res.data.uname
				this.uid = res.data.uid
				this.curr_worktype_bh = res.data.curr_worktype_bh
				this.curr_worktype_name = res.data.curr_worktype_name				
			}			
		})
	},
	methods: {
		onpeRegister(){
				uni.showModal({
					title: '提示',
					content: '是否注销用户',
					success: function (res) {
						if (res.confirm) {
							uni.removeStorage({
								key: 'loginData',success: function (res) {
									uni.redirectTo({
										url:'../login/login'
									})
								}
							});
							
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});			
		},
		onpeChangePassword(){
			uni.navigateTo({
				url:'../admin/ChangePassword/ChangePassword'
			})
		},
		btnVersion(){










		}
	}
}
